<template>
  <main class="noselect screen-centered">
    <div id="stack" :class="{ incomplete: stack.length > 3 }">
      <span v-for="(value, index) in stack_shown" :key="index">
        {{ prettyPrint(value) }}
      </span>
    </div>
    <div id="input">{{ inputfield }}</div>
  </main>
</template>

<script>
import { Decimal } from "decimal.js";

export default {
  data: function () {
    return {
      stack: [],
      inputfield: "",
    };
  },
  mounted() {
    document.addEventListener("keydown", this.keypressed);
  },
  beforeDestroy() {
    document.removeEventListener("keydown", this.keypressed);
  },
  computed: {
    stack_shown: function () {
      return this.stack.slice(-3);
    },
  },
  methods: {
    prettyPrint: function (number) {
      let maxlen = 20;
      let repr = number.toFixed();
      if (repr.length <= maxlen) return repr;
      repr = number.toPrecision(maxlen - 1);
      if (repr.length > maxlen) repr = number.toExponential(maxlen);
      if (repr.includes("e")) {
        let sp = repr.split("e");
        return number.toExponential(maxlen - 3 - sp[1].length);
      }
      return repr;
    },
    keypressed: function (ev) {
      let maxlength = 20;
      let value = ev.key;
      if (value >= "0" && value <= "9" && this.inputfield.length < maxlength) {
        this.inputfield += value;
      } else if (value == ".") {
        if (
          !isNaN(Number(this.inputfield + ".")) &&
          this.inputfield.length < maxlength
        )
          this.inputfield += ".";
      } else if (value == "Backspace") {
        if (this.inputfield !== "")
          this.inputfield = this.inputfield.slice(0, -1);
        else this.stack.pop();
      } else if (value == "Enter") {
        if (this.inputfield !== "" && !isNaN(Number(this.inputfield))) {
          this.stack.push(new Decimal(this.inputfield));
          this.inputfield = "";
        }
      } else if (value == "Tab") {
        if (this.stack.length > 1) {
          let x = this.stack.pop();
          let y = this.stack.pop();
          this.stack.push(x);
          this.stack.push(y);
        }
      } else if ("+-*/".includes(value)) {
        this.binaryOperation(value);
      } else {
        return;
      }
      ev.preventDefault();
      ev.stopPropagation();
    },
    pushOrFail: function () {
      if (this.inputfield === "") return true;
      if (!isNaN(Number(this.inputfield))) {
        this.stack.push(new Decimal(this.inputfield));
        this.inputfield = "";
        return true;
      }
      return false;
    },
    binaryOperation: function (key) {
      if (this.pushOrFail() === false) return null;
      if (this.stack.length < 2) return null;
      let x = this.stack.pop();
      let y = this.stack.pop();
      switch (key) {
        case "+":
          this.stack.push(y.plus(x));
          break;
        case "-":
          this.stack.push(y.minus(x));
          break;
        case "*":
          this.stack.push(y.times(x));
          break;
        case "/":
          this.stack.push(y.dividedBy(x));
          break;
      }
    },
  },
};
</script>

